@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

div.details {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  min-width: 100%;
}

div.details div.graph {
  display: flex;
  height: 180px;
  width: 500px;
  position: relative;
  align-self: center;
  border-radius: 3px;
  margin-bottom: 30px;
}

div.graph div.stats {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 50px;
  top: 8px;
}

div.stats p.heading {
  font-size: 14px;
  font-weight: 700;
  margin: 0;
}

div.stats p.x-value {
  font-size: 12px;
  font-weight: 700;
  margin: 0;
}

div.stats p.y-value {
  font-size: 20px;
  font-weight: 700;
  margin: 0;
}

div.graph--confirmed {
  background-color: #f59e9ec4;
}

div.graph--confirmed div.stats {
  color: #ec0c0cde;
}

div.graph--active {
  background-color: #9cbfe2c4;
}

div.graph--active div.stats {
  color: #036bd3d8;
}

div.graph--recovered {
  background-color: #aedbb2cc;
}

div.graph--recovered div.stats {
  color: #10aa20f1;
}

div.graph--deaths {
  background-color: #d8d8d8ce;
}

div.graph--deaths div.stats {
  color: #868686;
}

div.buttons button {
  border: none;
  background-color: #f7c72bd8;
  height: 50px;
  font-size: 14px;
  padding: 5px 16px 5px 16px;
  margin-bottom: 50px;
  margin-top: 20px;
}

div.buttons button:focus{
  background-color: #f0bd17;
}

@media (max-width: 480px) {
  div.details {
    min-width: 100%;
  }
  div.details div.graph {
    height: 150px;
    width: 100%;
  }
}
div.summary {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  max-width: 100%;
}

div.summary div.card {
  display: flex;
  flex-direction: column;
  height: 150px;
  max-width: 180px;
  padding: 0 20px;
  border-radius: 3px;
  align-items: center;
}

div.card p.heading {
  font-size: 14px;
  font-weight: 400;
}

div.card p.change {
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 700;
}

div.card p.total {
  margin-top: 7px;
  font-size: 20px;
  font-weight: 700;
}

.card--confirmed {
  color: #e90000;
}

.card--confirmed p.change {
  color: #e90000b6;
}

div.summary div.card--confirmed:hover {
  background-color: #f59e9ec4;
}

.card--active {
  color: #0075eb;
}

.card--active p.change {
  color: #0076ebb6;
}

div.summary div.card--active:hover {
  background-color: #9cbfe2c4;
}

.card--recovered {
  color: #15B325;
}

.card--recovered p.change {
  color: #15b325b6;
}

div.summary div.card--recovered:hover {
  background-color: #aedbb2cc;
}

.card--deceased {
  color: #808080;
}

.card--deceased p.change {
  color: #868686b6;
}

div.summary div.card--deceased:hover {
  background-color: #d8d8d8ce;
}

@media(max-width: 480px) {
  div.summary {
    flex-direction: column;
    align-items: center;
  }
}
html,
body,
#root {
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  font-size: 16px;
  box-sizing: border-box;
  margin: 0;
  min-width: 100%;
}

div.App {
  display: flex;
  flex-direction: column;
}

div.search {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 15px 0 30px 0;
  max-width: 100%;
}

div.search label {
  color: #6d6969d2;
}

div.search div.search-input {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  padding: 10px;
  margin-top: 40px;
  max-width: 450px;
  box-shadow: 3px 10px 30px #bebcbca6;
}

div.search-input .search-bar {
  border: none;
  background-color: white;
  box-sizing: border-box;
  max-width: 400px;
  height: 50px;
  margin-left: 6px;
  padding: 0 10px;
  color: #6d6969d2;
  font-size: 18px;
  font-weight: 400;
  -webkit-appearance: none;
}

@media (max-width: 480px) {
  div.search-input {
    margin: auto;
  }
  .search-bar {
    margin: 0 auto 0 6px ;
    width: 80%;
  }
}

